/* delivery table styling */

.bootstrapTable1 {

  font-family: "Bungee", cursive,"Trebuchet MS", Arial, Helvetica, sans-serif;
  color: #FFFFFF !important;  
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  text-rendering: optimizeLegibility;
	-webkit-font-feature-settings: "liga", "kern";
}

.bootstrapTable1 th, .bootstrapTable1 td {
  
  color: #FFFFFF !important;
  border: 5px !important;  
}

.bootstrapTable1 tr:nth-child(even){
  background-color: #322f31;
}

.bootstrapTable1 tr:hover {
  background-color: #424042 !important;
  transition: all 0.1s ease;
}


@media all and (max-width:35em){
  .bootstrapTable1 th.mobile, .bootstrapTable1 td.mobile{
  background-color: rgb(231, 228, 12);
  display:none 
  }
} 

